import { ColorsArr, calcTileIndex } from "../settings/game-funcs";
import Ball from "../components/ball";
import Board from "../components/board";
import Settings from "../settings/static-settings";
var Game = /** @class */ (function () {
    //   private ballArr: Array<Array<number | string>> = [];
    //   private balls: Array<Array<Ball>> = [];
    function Game() {
        this.BoardClass = new Board();
        this.BoardClass.render();
        this.board = document.getElementById("container");
        for (var i = 0; i < 9; i++) {
            Settings.ballArr.push([]);
            for (var j = 0; j < 9; j++)
                Settings.ballArr[i].push(0);
        }
        for (var i = 0; i < 9; i++) {
            Settings.balls.push([]);
            for (var j = 0; j < 9; j++)
                Settings.balls[i].push(null);
        }
        this.drawBalls();
    }
    Game.prototype.drawBalls = function () {
        for (var i = 0; i < 18; i++) {
            var colorIndex = Math.round(Math.random() * 7);
            colorIndex > 6 ? colorIndex-- : null;
            console.log(ColorsArr[colorIndex]);
            while (true) {
                var x = Math.floor(Math.random() * 9);
                var y = Math.floor(Math.random() * 9);
                if (Settings.ballArr[y][x] == 0) {
                    Settings.ballArr[y][x] = ColorsArr[colorIndex][0].toUpperCase();
                    Settings.balls[y][x] = new Ball(ColorsArr[colorIndex], ColorsArr[colorIndex][0].toUpperCase(), { x: x, y: y });
                    var endIndex = calcTileIndex(x + "_" + y);
                    var tile = document.getElementById(x + "_" + y + "-" + endIndex);
                    tile.appendChild(Settings.balls[y][x].render());
                    break;
                    //   this.
                }
            }
            console.log(Settings.balls);
            console.log(Settings.ballArr);
        }
    };
    return Game;
}());
export default Game;
